.about-card {
    display: flex;
    flex-direction: row;
    margin-bottom: 6rem;
    margin-left: 5vw;
    padding-top: 3rem;
    
}

.about-icons {
    font-size: 4rem;
    margin-right: 1vw;
}

.about-card-name {
    color: var(--red);
    font-family: Righteous;
    margin: 0;
    text-transform: uppercase;
}

.about-card-info {
    color: var(--light-gray);
    font-weight: 300;
    margin-top: 10px;
}


.about-container {
    display: flex;
    flex-wrap: wrap;
    height: 80vh;
    justify-content: space-around;
    margin-top: 2vh;
}

.about-container>* {
    flex: 1 0 300px;
}

.about-description {
    line-height: 1.5;
    font-size: 1.8rem;
    font-weight: 300;
    padding-top: 20px;
    text-align: justify;
}

.about-dot {
    color: var(--red);
    font-size: 8rem;
}

.about-title {
    color: var(--black);
    font-family: Righteous;
    font-size: var(--xxl-size);
    font-weight: 400;
    margin-bottom: 0;
    margin-top: 0;
}

.about-skills {
    background-color: var(--red);
    margin: 10rem 0;
    padding: 3rem 2vw;
    width: 70%;
}

.about-skills p {
    color: white;
    font-size: var(--l-size);
    margin-top: 0;
}

.about-skills ul {
    padding-left: 20px;
}

.about-skills ul li {
    color: white;
    list-style-type: none;
    padding-bottom: 10px;
    font-weight: 300;
    font-size: 2.0rem;
}

.about-subtitle {
    color: var(--light-gray);
    font-size: 2.0rem;
    font-weight: 500;
    margin-top: 0;
    width: 70%;
}