@import url(https://fonts.googleapis.com/css2?family=Righteous&family=Roboto:ital,wght@0,300;0,500;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
:root {
    /*Spacing*/
    --s-size: 1.2rem;
    --m-size: 1.6rem;
    --l-size: 3.2rem;
    --xl-size: 4.8rem;
    --xxl-size: 6.0rem;
    --desktop-breakpoint: 45rem;

    /* Colors */
    --black: #2c2c2c;
    --dark-gray: #707070;
    --light-gray: #A4A4A4;
    --red: #FF4D4D;
    --light-red: #FF6A6A;

}
html{
    font-size: 62.5%;
}

body{
    background-color: #f7f7f7;
    font-family: roboto;
    font-size: var(--m-size);
    
}

button {
    cursor: pointer;
}

button:disabled{
    cursor: default;
}


::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}

.container {
    padding: 0 4vw;
}

.logo {
    color: var(--black);
    font-family: 'Righteous';
    font-weight: 500;
    padding: 0rem 3rem;
   
}
.header{
    align-items: center;
    display: flex;
    font-family: "Righteous";
    font-weight: 600;
    font-size: 2rem;
    justify-content: center;

}

.header > * {
    color: var(--light-gray);
    margin: 0rem 2vw;
    padding: 0 10px 10px 10px;
    position: relative;
    text-decoration: none;
    transition: all 0.3s ease-in-out 0s;
}

.header > *:hover {
    color: var(--light-red);
}

.header > *::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: var(--dark-gray);
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
}

.header > *:hover::before{
    visibility: visible;
    transform: scaleX(1);
} 


.about-card {
    display: flex;
    flex-direction: row;
    margin-bottom: 6rem;
    margin-left: 5vw;
    padding-top: 3rem;
    
}

.about-icons {
    font-size: 4rem;
    margin-right: 1vw;
}

.about-card-name {
    color: var(--red);
    font-family: Righteous;
    margin: 0;
    text-transform: uppercase;
}

.about-card-info {
    color: var(--light-gray);
    font-weight: 300;
    margin-top: 10px;
}


.about-container {
    display: flex;
    flex-wrap: wrap;
    height: 80vh;
    justify-content: space-around;
    margin-top: 2vh;
}

.about-container>* {
    flex: 1 0 300px;
}

.about-description {
    line-height: 1.5;
    font-size: 1.8rem;
    font-weight: 300;
    padding-top: 20px;
    text-align: justify;
}

.about-dot {
    color: var(--red);
    font-size: 8rem;
}

.about-title {
    color: var(--black);
    font-family: Righteous;
    font-size: var(--xxl-size);
    font-weight: 400;
    margin-bottom: 0;
    margin-top: 0;
}

.about-skills {
    background-color: var(--red);
    margin: 10rem 0;
    padding: 3rem 2vw;
    width: 70%;
}

.about-skills p {
    color: white;
    font-size: var(--l-size);
    margin-top: 0;
}

.about-skills ul {
    padding-left: 20px;
}

.about-skills ul li {
    color: white;
    list-style-type: none;
    padding-bottom: 10px;
    font-weight: 300;
    font-size: 2.0rem;
}

.about-subtitle {
    color: var(--light-gray);
    font-size: 2.0rem;
    font-weight: 500;
    margin-top: 0;
    width: 70%;
}
.contact-container {
    display: flex;
    align-items: flex-end;
    margin: 10vh 0;
}

.contact-container div {
    flex: 1 1;
    padding: 0 2.5vw;
}

.contact-container div:nth-child(2) {
    text-align: center;
}

.contact-description {
    color: var(--dark-gray);
    font-size: 2.2rem;
    font-weight: 300;
    line-height: 1.5;
    text-align: justify;
}

.contact-dot {
    color: var(--red);
    font-size: 8rem;
}

.contact-info {
    color: var(--light-gray);
    font-size: 2.2rem;
    font-weight: 400;
}

.contact-info a {
    color: var(--red);
    text-decoration: none;
}

.contact-networks {
    margin-bottom: 80px;
}

.contact-networks > * {
    font-size: 4rem;
    margin-right: 2vw;
    margin-top: 10px;
}

.contact-title {
    font-family: Righteous;
    font-size: var(--xl-size);
}

.contact-subtitle {
    color: var(--red);
    font-family: Righteous;
    font-size: var(--l-size);
    margin: 0;
}


.home-avatar {
    width: 40vw;
}

.home-container {
    align-items: flex-start;
    color: var(--black);
    display: flex;
    height: 70vh;
    justify-content: space-around;
    margin-top: 20vh;
}

.home-dot {
    color: var(--red);
    font-size: 8rem;
}

.home-title {
    font-family: Righteous;
    font-size: var(--xxl-size);
    margin: 0;
}

.home-title--sub {
    font-family: roboto mono;
    margin-bottom: 10px;
    margin-left: 2rem;
}

.home-title--sub-container {
    align-items: flex-end;
    display: flex;
}

.home-subtitle {
    color: var(--light-gray);
    font-size: 2.2rem;
    margin: 16px;
}

.home-subtitle>* {
    color: var(--red);
    text-decoration: none;
}
.work-container {
    display: flex;
  flex-wrap: wrap;
}

.work-container > * {
    flex: 1 0 33%;
    text-align: center;
}

.work-dot{
    color: var(--red);
    font-size: 8rem;
}

.work-title{
    color: var(--black);
    font-family: Righteous;
    font-size: var(--xl-size);
    font-weight: 400;
    text-align: left;
}
.workItem-name {
    text-align: center;
    font-size: var(--m-size);
    font-weight: 600;
    margin: 0;
}

.workItem-info {
    color: var(--dark-gray);
    font-family: roboto mono;
    font-size: var(--s-size);
    margin-top: 5px;

}

