.home-avatar {
    width: 40vw;
}

.home-container {
    align-items: flex-start;
    color: var(--black);
    display: flex;
    height: 70vh;
    justify-content: space-around;
    margin-top: 20vh;
}

.home-dot {
    color: var(--red);
    font-size: 8rem;
}

.home-title {
    font-family: Righteous;
    font-size: var(--xxl-size);
    margin: 0;
}

.home-title--sub {
    font-family: roboto mono;
    margin-bottom: 10px;
    margin-left: 2rem;
}

.home-title--sub-container {
    align-items: flex-end;
    display: flex;
}

.home-subtitle {
    color: var(--light-gray);
    font-size: 2.2rem;
    margin: 16px;
}

.home-subtitle>* {
    color: var(--red);
    text-decoration: none;
}